.user-post-card {
    &:last-of-type{
        margin-bottom: 0;
    }
    background: white;
    margin-bottom: 30px;
    ul {
        list-style: none;
    }

    justify-content: space-between;

    .top-wrapper {
        display: flex;
        justify-content: space-between;
        padding: 23px 17px;
        border-bottom: 1px solid #414D6A;

        .user-post-card-left {
            display: flex;
            align-items: center;

            img {
                width: 50px;
                height: 50px;
            }

            h3 {
                font-size: 17px;
                color: #414D6A;
            }
        }

        .user-post-card-right {
            display: flex;
            align-items: center;

            .box {
                ul {
                    font-family: 'Gilroy bold', sans-serif;

                    li {
                        font-weight: lighter;
                        font-size: 15px;
                        line-height: 18px;
                        color: #414D6A;

                    }


                }

                margin-left: 40px;

                button {
                    background: transparent;
                    border: none;
                    outline: none;
                }

                .add {
                    padding: 10px 20px 10px 47px;
                    border: 1px solid #41D481;
                    border-radius: 100px 5px 5px 100px;
                    font-size: 16px;
                    color: #000000;
                    position: relative;

                    &::before {
                        content: url(../../assets/images/arrow-up.svg);
                        background: #41D481;
                        position: absolute;
                        width: 34px;
                        padding-top: 5px;
                        height: 34px;
                        border-radius: 50%;
                        bottom: 0;
                        left: 0;
                    }

                    &.blue {
                        margin-left: 30px;
                        border: 1px solid #304FFF;

                        &::before {
                            background: #304FFF;
                        }
                    }
                }

                .other-menu {
                    padding: 15px 9px;
                    border-radius: 50%;
                    margin-left: 30px;
                    background: #FFFFFF;
                    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.196643);
                }
            }
        }
    }

    .user-post-card-bottom {
        width: 100%;
        padding: 8px 22px;

        ul {
            display: flex;

            .active {
                color: #304FFF;
            }

            li {
                margin-right: 15px;
                font-size: 14px;
                line-height: 17px;
                color: black;
            }
        }
    }
}