.footer {
    width: 100%;
    background: #282B30;
    padding: 22px 0;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .box {
            display: flex;
            align-items: center;

            h3 {
                margin-left: 14px;
                font-family: 'Gilroy bold', sans-serif;
                font-weight: bold;
                font-size: 14px;
                color: white;
            }

            p {
                width: 307px;
                font-weight: 500;
                font-size: 15px;
                line-height: 18px;
                color: #98A4B7;
            }
            img{
                &:first-of-type{
                    margin-right: 10px;
                }
            }
        }
    }
}